// moment.js locale configuration
// locale : Japanese [ja]
// author : LI Long : https://github.com/baryon

import { LocaleData } from '../locale/locale.class';

export const ja: LocaleData = {
  abbr: 'ja',
  months: '1_2_3_4_5_6_7_8_9_10_11_12'.split('_'),
  monthsShort: '1_2_3_4_5_6_7_8_9_10_11_12'.split('_'),
  weekdays: '______'.split('_'),
  weekdaysShort: '______'.split('_'),
  weekdaysMin: '______'.split('_'),
  longDateFormat: {
    LT: 'HH:mm',
    LTS: 'HH:mm:ss',
    L: 'YYYY/MM/DD',
    LL: 'YYYYMD',
    LLL: 'YYYYMD HH:mm',
    LLLL: 'YYYYMD HH:mm dddd',
    l: 'YYYY/MM/DD',
    ll: 'YYYYMD',
    lll: 'YYYYMD HH:mm',
    llll: 'YYYYMD HH:mm dddd'
  },
  meridiemParse: /|/i,
  isPM(input: string): boolean {
    return input === '';
  },
  meridiem(hour: number): string {
    return hour < 12 ? '' : '';
  },
  calendar: {
    sameDay: '[] LT',
    nextDay: '[] LT',
    nextWeek: '[]dddd LT',
    lastDay: '[] LT',
    lastWeek: '[]dddd LT',
    sameElse: 'L'
  },
  dayOfMonthOrdinalParse: /\d{1,2}/,
  ordinal(num: number, period: string): string {
    switch (period) {
      case 'd':
      case 'D':
      case 'DDD':
        return `${num}`;
      default:
        return num.toString();
    }
  },
  relativeTime: {
    future: '%s',
    past: '%s',
    s: '',
    m: '1',
    mm: '%d',
    h: '1',
    hh: '%d',
    d: '1',
    dd: '%d',
    M: '1',
    MM: '%d',
    y: '1',
    yy: '%d'
  }
};
