// moment.js locale configuration
// locale : Hindi [hi]
// author : Mayank Singhal : https://github.com/mayanksinghal

import { LocaleData } from '../locale/locale.class';

const symbolMap: any = {
  1: '',
  2: '',
  3: '',
  4: '',
  5: '',
  6: '',
  7: '',
  8: '',
  9: '',
  0: ''
};
const numberMap: any = {
  '': '1',
  '': '2',
  '': '3',
  '': '4',
  '': '5',
  '': '6',
  '': '7',
  '': '8',
  '': '9',
  '': '0'
};

export const hi: LocaleData = {
  abbr: 'hi',
  months: '___________'.split(
    '_'
  ),
  monthsShort: '._.__.___._._._._._.'.split(
    '_'
  ),
  monthsParseExact: true,
  weekdays: '______'.split('_'),
  weekdaysShort: '______'.split('_'),
  weekdaysMin: '______'.split('_'),
  longDateFormat: {
    LT: 'A h:mm ',
    LTS: 'A h:mm:ss ',
    L: 'DD/MM/YYYY',
    LL: 'D MMMM YYYY',
    LLL: 'D MMMM YYYY, A h:mm ',
    LLLL: 'dddd, D MMMM YYYY, A h:mm '
  },
  calendar: {
    sameDay: '[] LT',
    nextDay: '[] LT',
    nextWeek: 'dddd, LT',
    lastDay: '[] LT',
    lastWeek: '[] dddd, LT',
    sameElse: 'L'
  },
  relativeTime: {
    future: '%s ',
    past: '%s ',
    s: '  ',
    m: ' ',
    mm: '%d ',
    h: ' ',
    hh: '%d ',
    d: ' ',
    dd: '%d ',
    M: ' ',
    MM: '%d ',
    y: ' ',
    yy: '%d '
  },
  preparse(str: string): string {
    return str.replace(/[]/g, function (match) {
      return numberMap[match];
    });
  },
  postformat(str: string): string {
    return str.replace(/\d/g, function (match) {
      return symbolMap[match];
    });
  },
  // Hindi notation for meridiems are quite fuzzy in practice. While there
  // exists a rigid notion of a 'Pahar' it is not used as rigidly in modern
  // Hindi.
  meridiemParse: /|||/,
  meridiemHour(_hour: number, meridiem: string): number {
    const hour = _hour === 12 ? 0 : _hour;
    // tslint:disable
    if (meridiem === '') {
      return hour < 4 ? hour : hour + 12;
    } else if (meridiem === '') {
      return hour;
    } else if (meridiem === '') {
      return hour >= 10 ? hour : hour + 12;
    } else if (meridiem === '') {
      return hour + 12;
    }
  },
  meridiem(hour: number): string {
    if (hour < 4) {
      return '';
    } else if (hour < 10) {
      return '';
    } else if (hour < 17) {
      return '';
    } else if (hour < 20) {
      return '';
    } else {
      return '';
    }
  },
  week: {
    dow: 0, // Sunday is the first day of the week.
    doy: 6 // The week that contains Jan 1st is the first week of the year.
  }
};
